:root {
  --font-body: "Open Sans", Arial, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

@keyframes flip-in {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(-90deg);
  }
}
@keyframes flip-out {
  0% {
    transform: rotateX(-90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}
.flip-in {
  animation: flip-in 0.5s;
}

.flip-out {
  animation: flip-out 0.5s;
}

@keyframes blink {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.blink {
  animation: blink 0.5s;
}

html {
  font-size: 100%;
  font-family: var(--font-body);
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  touch-action: manipulation;
}

body {
  margin: 0;
  padding: 1rem 0rem;
  background-color: #111112;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
}

.line {
  border: 0.00625rem solid white;
  width: 100%;
  margin-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  .line {
    margin-bottom: 3.125rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1;
}

h1 {
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.guess {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.3125rem;
  width: 18.75rem;
  padding-bottom: 0.3125rem;
  transform: translateX(-1.5625rem);
}
.guess__tile {
  border: 0.125rem solid #424242;
  width: 4.0625rem;
  height: 4.0625rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: 600;
}
.guess .has-letter {
  border-color: #767474;
}
.guess .correct {
  background-color: #008f00;
  border-color: #008f00;
}
.guess .present {
  background-color: #b59f3b;
  border-color: #b59f3b;
}
.guess .absent {
  background-color: #2e2e2e;
  border-color: #2e2e2e;
}

.keyboard {
  padding-top: 1.875rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.3125rem;
}

.row .correct {
  background-color: #008f00;
  border-color: #008f00;
}
.row .absent {
  background-color: #2e2e2e;
  border-color: #2e2e2e;
}

button {
  cursor: pointer;
  width: 2.1875rem;
  height: 3.125rem;
  border: none;
  border-radius: 15%;
  background-color: #999999;
  text-transform: uppercase;
  color: white;
  font-family: var(--font-body);
  font-weight: 600;
  font-size: 0.75rem;
}
button:hover {
  background-color: #7d7d7d;
  transition: all 2ms ease-in-out;
}
@media (min-width: 40em) {
  button {
    width: 2.8125rem;
    height: 3.4375rem;
    font-size: 0.9375rem;
  }
}
@media (min-width: 71.875em) {
  button {
    width: 3.125rem;
    height: 3.75rem;
  }
}

#del, #enter {
  width: 3.40625rem;
}
@media (min-width: 40em) {
  #del, #enter {
    width: 4.46875rem;
  }
}
@media (min-width: 71.875em) {
  #del, #enter {
    width: 4.84375rem;
  }
}